:root{--color-purple: #4b145b;--color-grey: #eae6eb;--color-grey2: #324d67}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;box-sizing:border-box}@media (max-width: 50em){html{font-size:45%}}body{font-size:1.5rem;background-color:var(--color-purple)}.container{display:flex;background-color:#fff;margin:7rem auto;width:68vw;padding:2rem}@media (max-width: 75em){.container{flex-direction:column;gap:3rem;width:90vw}}.heading{flex-basis:30%;padding:1rem}.heading-h{font-size:3rem;text-transform:capitalize;font-family:"Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;font-weight:400}.accordion-list{flex-basis:70%;display:flex;flex-direction:column;gap:2rem}.accordion{border:2px solid var(--color-grey);padding:2rem;font-size:1.8rem;display:flex;flex-direction:column;gap:1rem;box-shadow:0 0.6rem 3rem rgba(0,0,0,0.2)}.accordion__heading{display:flex;justify-content:space-between}.accordion__heading--question{font-weight:900;word-spacing:2px;letter-spacing:1px}.accordion__heading--icon{width:3.5rem;height:3.5rem;border-radius:50%;background-color:var(--color-grey);position:relative;cursor:pointer}.accordion__heading--icon span{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:3.4rem;color:lightcoral}.accordion__text{line-height:1.5;color:var(--color-grey2)}.hidden{display:none}
